module.exports = {"Calendar":{"description":"react-big-calendar is a full featured Calendar component for managing events and dates. It uses\nmodern `flexbox` for layout, making it super responsive and performant. Leaving most of the layout heavy lifting\nto the browser. __note:__ The default styles use `height: 100%` which means your container must set an explicit\nheight (feel free to adjust the styles to suit your specific needs).\n\nBig Calendar is unopiniated about editing and moving events, preferring to let you implement it in a way that makes\nthe most sense to your app. It also tries not to be prescriptive about your event data structures, just tell it\nhow to find the start and end datetimes and you can pass it whatever you want.\n\nOne thing to note is that, `react-big-calendar` treats event start/end dates as an _exclusive_ range.\nwhich means that the event spans up to, but not including, the end date. In the case\nof displaying events on whole days, end dates are rounded _up_ to the next day. So an\nevent ending on `Apr 8th 12:00:00 am` will not appear on the 8th, whereas one ending\non `Apr 8th 12:01:00 am` will. If you want _inclusive_ ranges consider providing a\nfunction `endAccessor` that returns the end date + 1 day for those events that end at midnight.","displayName":"Calendar","methods":[{"name":"getContext","docblock":null,"modifiers":[],"params":[{"name":"{\n  startAccessor,\n  endAccessor,\n  allDayAccessor,\n  tooltipAccessor,\n  titleAccessor,\n  resourceAccessor,\n  resourceIdAccessor,\n  resourceTitleAccessor,\n  eventPropGetter,\n  slotPropGetter,\n  dayPropGetter,\n  view,\n  views,\n  localizer,\n  culture,\n  messages = {},\n  components = {},\n  formats = {},\n}","type":null}],"returns":null},{"name":"getViews","docblock":null,"modifiers":[],"params":[],"returns":null},{"name":"getView","docblock":null,"modifiers":[],"params":[],"returns":null},{"name":"getDrilldownView","docblock":null,"modifiers":[],"params":[{"name":"date","type":null}],"returns":null},{"name":"handleRangeChange","docblock":"@param date\n@param viewComponent\n@param {'month'|'week'|'work_week'|'day'|'agenda'} [view] - optional\nparameter. It appears when range change on view changing. It could be handy\nwhen you need to have both: range and view type at once, i.e. for manage rbc\nstate via url","modifiers":[],"params":[{"name":"date","description":null,"type":null},{"name":"viewComponent","description":null,"type":null},{"name":"view"}],"returns":null,"description":null},{"name":"handleNavigate","docblock":null,"modifiers":[],"params":[{"name":"action","type":null},{"name":"newDate","type":null}],"returns":null},{"name":"handleViewChange","docblock":null,"modifiers":[],"params":[{"name":"view","type":null}],"returns":null},{"name":"handleSelectEvent","docblock":null,"modifiers":[],"params":[{"name":"...args","type":null}],"returns":null},{"name":"handleDoubleClickEvent","docblock":null,"modifiers":[],"params":[{"name":"...args","type":null}],"returns":null},{"name":"handleSelectSlot","docblock":null,"modifiers":[],"params":[{"name":"slotInfo","type":null}],"returns":null},{"name":"handleDrillDown","docblock":null,"modifiers":[],"params":[{"name":"date","type":null},{"name":"view","type":null}],"returns":null}],"props":{"localizer":{"type":{"name":"object"},"required":true,"description":"","name":"localizer","docblock":"","doclets":{},"descriptionHtml":""},"elementProps":{"type":{"name":"object"},"required":false,"description":"Props passed to main calendar `<div>`.","defaultValue":{"value":"{}","computed":false},"name":"elementProps","docblock":"Props passed to main calendar `<div>`.","doclets":{},"descriptionHtml":"<p>Props passed to main calendar <code>&lt;div&gt;</code>.</p>\n"},"date":{"type":{"name":"instanceOf","value":"Date"},"required":false,"description":"The current date value of the calendar. Determines the visible view range.\nIf `date` is omitted then the result of `getNow` is used; otherwise the\ncurrent date is used.","name":"date","docblock":"The current date value of the calendar. Determines the visible view range.\nIf `date` is omitted then the result of `getNow` is used; otherwise the\ncurrent date is used.\n\n@controllable onNavigate","doclets":{"controllable":"onNavigate"},"descriptionHtml":"<p>The current date value of the calendar. Determines the visible view range.\nIf <code>date</code> is omitted then the result of <code>getNow</code> is used; otherwise the\ncurrent date is used.</p>\n"},"view":{"type":{"name":"string"},"required":false,"description":"The current view of the calendar.","defaultValue":{"value":"views.MONTH","computed":true},"name":"view","docblock":"The current view of the calendar.\n\n@default 'month'\n@controllable onView","doclets":{"default":"'month'","controllable":"onView"},"descriptionHtml":"<p>The current view of the calendar.</p>\n"},"defaultView":{"type":{"name":"Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')"},"required":false,"description":"The initial view set for the Calendar.","name":"defaultView","docblock":"The initial view set for the Calendar.\n@type Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')\n@default 'month'","doclets":{"type":"Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')","default":"'month'"},"descriptionHtml":"<p>The initial view set for the Calendar.</p>\n"},"events":{"type":{"name":"arrayOf","value":{"name":"object"}},"required":false,"description":"An array of event objects to display on the calendar. Events objects\ncan be any shape, as long as the Calendar knows how to retrieve the\nfollowing details of the event:\n\n - start time\n - end time\n - title\n - whether its an \"all day\" event or not\n - any resource the event may be related to\n\nEach of these properties can be customized or generated dynamically by\nsetting the various \"accessor\" props. Without any configuration the default\nevent should look like:\n\n```js\nEvent {\n  title: string,\n  start: Date,\n  end: Date,\n  allDay?: boolean\n  resource?: any,\n}\n```","name":"events","docblock":"An array of event objects to display on the calendar. Events objects\ncan be any shape, as long as the Calendar knows how to retrieve the\nfollowing details of the event:\n\n - start time\n - end time\n - title\n - whether its an \"all day\" event or not\n - any resource the event may be related to\n\nEach of these properties can be customized or generated dynamically by\nsetting the various \"accessor\" props. Without any configuration the default\nevent should look like:\n\n```js\nEvent {\n  title: string,\n  start: Date,\n  end: Date,\n  allDay?: boolean\n  resource?: any,\n}\n```","doclets":{},"descriptionHtml":"<p>An array of event objects to display on the calendar. Events objects\ncan be any shape, as long as the Calendar knows how to retrieve the\nfollowing details of the event:</p>\n<ul>\n<li>start time</li>\n<li>end time</li>\n<li>title</li>\n<li>whether its an &quot;all day&quot; event or not</li>\n<li>any resource the event may be related to</li>\n</ul>\n<p>Each of these properties can be customized or generated dynamically by\nsetting the various &quot;accessor&quot; props. Without any configuration the default\nevent should look like:</p>\n<pre><code class=\"lang-js\">Event <span class=\"token punctuation\">{</span>\n  title<span class=\"token punctuation\">:</span> string<span class=\"token punctuation\">,</span>\n  start<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n  end<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n  allDay<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> boolean\n  resource<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> any<span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n"},"titleAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Accessor for the event title, used to display event information. Should\nresolve to a `renderable` value.\n\n```js\nstring | (event: Object) => string\n```","defaultValue":{"value":"'title'","computed":false},"name":"titleAccessor","docblock":"Accessor for the event title, used to display event information. Should\nresolve to a `renderable` value.\n\n```js\nstring | (event: Object) => string\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Accessor for the event title, used to display event information. Should\nresolve to a <code>renderable</code> value.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> string\n</code></pre>\n"},"tooltipAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Accessor for the event tooltip. Should\nresolve to a `renderable` value. Removes the tooltip if null.\n\n```js\nstring | (event: Object) => string\n```","defaultValue":{"value":"'title'","computed":false},"name":"tooltipAccessor","docblock":"Accessor for the event tooltip. Should\nresolve to a `renderable` value. Removes the tooltip if null.\n\n```js\nstring | (event: Object) => string\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Accessor for the event tooltip. Should\nresolve to a <code>renderable</code> value. Removes the tooltip if null.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> string\n</code></pre>\n"},"allDayAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Determines whether the event should be considered an \"all day\" event and ignore time.\nMust resolve to a `boolean` value.\n\n```js\nstring | (event: Object) => boolean\n```","defaultValue":{"value":"'allDay'","computed":false},"name":"allDayAccessor","docblock":"Determines whether the event should be considered an \"all day\" event and ignore time.\nMust resolve to a `boolean` value.\n\n```js\nstring | (event: Object) => boolean\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Determines whether the event should be considered an &quot;all day&quot; event and ignore time.\nMust resolve to a <code>boolean</code> value.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> boolean\n</code></pre>\n"},"startAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"The start date/time of the event. Must resolve to a JavaScript `Date` object.\n\n```js\nstring | (event: Object) => Date\n```","defaultValue":{"value":"'start'","computed":false},"name":"startAccessor","docblock":"The start date/time of the event. Must resolve to a JavaScript `Date` object.\n\n```js\nstring | (event: Object) => Date\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>The start date/time of the event. Must resolve to a JavaScript <code>Date</code> object.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> Date\n</code></pre>\n"},"endAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"The end date/time of the event. Must resolve to a JavaScript `Date` object.\n\n```js\nstring | (event: Object) => Date\n```","defaultValue":{"value":"'end'","computed":false},"name":"endAccessor","docblock":"The end date/time of the event. Must resolve to a JavaScript `Date` object.\n\n```js\nstring | (event: Object) => Date\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>The end date/time of the event. Must resolve to a JavaScript <code>Date</code> object.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> Date\n</code></pre>\n"},"resourceAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Returns the id of the `resource` that the event is a member of. This\nid should match at least one resource in the `resources` array.\n\n```js\nstring | (event: Object) => Date\n```","defaultValue":{"value":"'resourceId'","computed":false},"name":"resourceAccessor","docblock":"Returns the id of the `resource` that the event is a member of. This\nid should match at least one resource in the `resources` array.\n\n```js\nstring | (event: Object) => Date\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Returns the id of the <code>resource</code> that the event is a member of. This\nid should match at least one resource in the <code>resources</code> array.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> Date\n</code></pre>\n"},"resources":{"type":{"name":"arrayOf","value":{"name":"object"}},"required":false,"description":"An array of resource objects that map events to a specific resource.\nResource objects, like events, can be any shape or have any properties,\nbut should be uniquly identifiable via the `resourceIdAccessor`, as\nwell as a \"title\" or name as provided by the `resourceTitleAccessor` prop.","name":"resources","docblock":"An array of resource objects that map events to a specific resource.\nResource objects, like events, can be any shape or have any properties,\nbut should be uniquly identifiable via the `resourceIdAccessor`, as\nwell as a \"title\" or name as provided by the `resourceTitleAccessor` prop.","doclets":{},"descriptionHtml":"<p>An array of resource objects that map events to a specific resource.\nResource objects, like events, can be any shape or have any properties,\nbut should be uniquly identifiable via the <code>resourceIdAccessor</code>, as\nwell as a &quot;title&quot; or name as provided by the <code>resourceTitleAccessor</code> prop.</p>\n"},"resourceIdAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Provides a unique identifier for each resource in the `resources` array\n\n```js\nstring | (resource: Object) => any\n```","defaultValue":{"value":"'id'","computed":false},"name":"resourceIdAccessor","docblock":"Provides a unique identifier for each resource in the `resources` array\n\n```js\nstring | (resource: Object) => any\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Provides a unique identifier for each resource in the <code>resources</code> array</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">resource<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> any\n</code></pre>\n"},"resourceTitleAccessor":{"type":{"name":"union","raw":"accessor","value":["func","string"]},"required":false,"description":"Provides a human readable name for the resource object, used in headers.\n\n```js\nstring | (resource: Object) => any\n```","defaultValue":{"value":"'title'","computed":false},"name":"resourceTitleAccessor","docblock":"Provides a human readable name for the resource object, used in headers.\n\n```js\nstring | (resource: Object) => any\n```\n\n@type {(func|string)}","doclets":{"type":"{(func|string)}"},"descriptionHtml":"<p>Provides a human readable name for the resource object, used in headers.</p>\n<pre><code class=\"lang-js\">string <span class=\"token operator\">|</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">resource<span class=\"token punctuation\">:</span> Object</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> any\n</code></pre>\n"},"getNow":{"type":{"name":"func"},"required":false,"description":"Determines the current date/time which is highlighted in the views.\n\nThe value affects which day is shaded and which time is shown as\nthe current time. It also affects the date used by the Today button in\nthe toolbar.\n\nProviding a value here can be useful when you are implementing time zones\nusing the `startAccessor` and `endAccessor` properties.","defaultValue":{"value":"() => new Date()","computed":false},"name":"getNow","docblock":"Determines the current date/time which is highlighted in the views.\n\nThe value affects which day is shaded and which time is shown as\nthe current time. It also affects the date used by the Today button in\nthe toolbar.\n\nProviding a value here can be useful when you are implementing time zones\nusing the `startAccessor` and `endAccessor` properties.\n\n@type {func}\n@default () => new Date()","doclets":{"type":"{func}","default":"() => new Date()"},"descriptionHtml":"<p>Determines the current date/time which is highlighted in the views.</p>\n<p>The value affects which day is shaded and which time is shown as\nthe current time. It also affects the date used by the Today button in\nthe toolbar.</p>\n<p>Providing a value here can be useful when you are implementing time zones\nusing the <code>startAccessor</code> and <code>endAccessor</code> properties.</p>\n"},"onNavigate":{"type":{"name":"func"},"required":false,"description":"Callback fired when the `date` value changes.","name":"onNavigate","docblock":"Callback fired when the `date` value changes.\n\n@controllable date","doclets":{"controllable":"date"},"descriptionHtml":"<p>Callback fired when the <code>date</code> value changes.</p>\n"},"onView":{"type":{"name":"func"},"required":false,"description":"Callback fired when the `view` value changes.","name":"onView","docblock":"Callback fired when the `view` value changes.\n\n@controllable view","doclets":{"controllable":"view"},"descriptionHtml":"<p>Callback fired when the <code>view</code> value changes.</p>\n"},"onDrillDown":{"type":{"name":"func"},"required":false,"description":"Callback fired when date header, or the truncated events links are clicked","name":"onDrillDown","docblock":"Callback fired when date header, or the truncated events links are clicked","doclets":{},"descriptionHtml":"<p>Callback fired when date header, or the truncated events links are clicked</p>\n"},"onRangeChange":{"type":{"name":"func"},"required":false,"description":"```js\n(dates: Date[] | { start: Date; end: Date }, view?: 'month'|'week'|'work_week'|'day'|'agenda') => void\n```\n\nCallback fired when the visible date range changes. Returns an Array of dates\nor an object with start and end dates for BUILTIN views. Optionally new `view`\nwill be returned when callback called after view change.\n\nCustom views may return something different.","name":"onRangeChange","docblock":"```js\n(dates: Date[] | { start: Date; end: Date }, view?: 'month'|'week'|'work_week'|'day'|'agenda') => void\n```\n\nCallback fired when the visible date range changes. Returns an Array of dates\nor an object with start and end dates for BUILTIN views. Optionally new `view`\nwill be returned when callback called after view change.\n\nCustom views may return something different.","doclets":{},"descriptionHtml":"<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span>dates<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">|</span> <span class=\"token punctuation\">{</span> start<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">;</span> end<span class=\"token punctuation\">:</span> Date <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> view<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> <span class=\"token string\">'month'</span><span class=\"token operator\">|</span><span class=\"token string\">'week'</span><span class=\"token operator\">|</span><span class=\"token string\">'work_week'</span><span class=\"token operator\">|</span><span class=\"token string\">'day'</span><span class=\"token operator\">|</span><span class=\"token string\">'agenda'</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token keyword\">void</span>\n</code></pre>\n<p>Callback fired when the visible date range changes. Returns an Array of dates\nor an object with start and end dates for BUILTIN views. Optionally new <code>view</code>\nwill be returned when callback called after view change.</p>\n<p>Custom views may return something different.</p>\n"},"onSelectSlot":{"type":{"name":"func"},"required":false,"description":"A callback fired when a date selection is made. Only fires when `selectable` is `true`.\n\n```js\n(\n  slotInfo: {\n    start: Date,\n    end: Date,\n    slots: Array<Date>,\n    action: \"select\" | \"click\" | \"doubleClick\",\n    bounds: ?{ // For \"select\" action\n      x: number,\n      y: number,\n      top: number,\n      right: number,\n      left: number,\n      bottom: number,\n    },\n    box: ?{ // For \"click\" or \"doubleClick\" actions\n      clientX: number,\n      clientY: number,\n      x: number,\n      y: number,\n    },\n  }\n) => any\n```","name":"onSelectSlot","docblock":"A callback fired when a date selection is made. Only fires when `selectable` is `true`.\n\n```js\n(\n  slotInfo: {\n    start: Date,\n    end: Date,\n    slots: Array<Date>,\n    action: \"select\" | \"click\" | \"doubleClick\",\n    bounds: ?{ // For \"select\" action\n      x: number,\n      y: number,\n      top: number,\n      right: number,\n      left: number,\n      bottom: number,\n    },\n    box: ?{ // For \"click\" or \"doubleClick\" actions\n      clientX: number,\n      clientY: number,\n      x: number,\n      y: number,\n    },\n  }\n) => any\n```","doclets":{},"descriptionHtml":"<p>A callback fired when a date selection is made. Only fires when <code>selectable</code> is <code>true</code>.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span>\n  slotInfo<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    start<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n    end<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n    slots<span class=\"token punctuation\">:</span> Array<span class=\"token operator\">&lt;</span>Date<span class=\"token operator\">></span><span class=\"token punctuation\">,</span>\n    action<span class=\"token punctuation\">:</span> <span class=\"token string\">\"select\"</span> <span class=\"token operator\">|</span> <span class=\"token string\">\"click\"</span> <span class=\"token operator\">|</span> <span class=\"token string\">\"doubleClick\"</span><span class=\"token punctuation\">,</span>\n    bounds<span class=\"token punctuation\">:</span> <span class=\"token operator\">?</span><span class=\"token punctuation\">{</span> <span class=\"token comment\">// For \"select\" action</span>\n      x<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      y<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      top<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      right<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      left<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      bottom<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n    box<span class=\"token punctuation\">:</span> <span class=\"token operator\">?</span><span class=\"token punctuation\">{</span> <span class=\"token comment\">// For \"click\" or \"doubleClick\" actions</span>\n      clientX<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      clientY<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      x<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n      y<span class=\"token punctuation\">:</span> number<span class=\"token punctuation\">,</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> any\n</code></pre>\n"},"onSelectEvent":{"type":{"name":"func"},"required":false,"description":"Callback fired when a calendar event is selected.\n\n```js\n(event: Object, e: SyntheticEvent) => any\n```","name":"onSelectEvent","docblock":"Callback fired when a calendar event is selected.\n\n```js\n(event: Object, e: SyntheticEvent) => any\n```\n\n@controllable selected","doclets":{"controllable":"selected"},"descriptionHtml":"<p>Callback fired when a calendar event is selected.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object<span class=\"token punctuation\">,</span> e<span class=\"token punctuation\">:</span> SyntheticEvent</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> any\n</code></pre>\n"},"onDoubleClickEvent":{"type":{"name":"func"},"required":false,"description":"Callback fired when a calendar event is clicked twice.\n\n```js\n(event: Object, e: SyntheticEvent) => void\n```","name":"onDoubleClickEvent","docblock":"Callback fired when a calendar event is clicked twice.\n\n```js\n(event: Object, e: SyntheticEvent) => void\n```","doclets":{},"descriptionHtml":"<p>Callback fired when a calendar event is clicked twice.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object<span class=\"token punctuation\">,</span> e<span class=\"token punctuation\">:</span> SyntheticEvent</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token keyword\">void</span>\n</code></pre>\n"},"onSelecting":{"type":{"name":"func"},"required":false,"description":"Callback fired when dragging a selection in the Time views.\n\nReturning `false` from the handler will prevent a selection.\n\n```js\n(range: { start: Date, end: Date }) => ?boolean\n```","name":"onSelecting","docblock":"Callback fired when dragging a selection in the Time views.\n\nReturning `false` from the handler will prevent a selection.\n\n```js\n(range: { start: Date, end: Date }) => ?boolean\n```","doclets":{},"descriptionHtml":"<p>Callback fired when dragging a selection in the Time views.</p>\n<p>Returning <code>false</code> from the handler will prevent a selection.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">range<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> start<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span> end<span class=\"token punctuation\">:</span> Date <span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token operator\">?</span>boolean\n</code></pre>\n"},"onShowMore":{"type":{"name":"func"},"required":false,"description":"Callback fired when a +{count} more is clicked\n\n```js\n(events: Object, date: Date) => any\n```","name":"onShowMore","docblock":"Callback fired when a +{count} more is clicked\n\n```js\n(events: Object, date: Date) => any\n```","doclets":{},"descriptionHtml":"<p>Callback fired when a +{count} more is clicked</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">events<span class=\"token punctuation\">:</span> Object<span class=\"token punctuation\">,</span> date<span class=\"token punctuation\">:</span> Date</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> any\n</code></pre>\n"},"selected":{"type":{"name":"object"},"required":false,"description":"The selected event, if any.","name":"selected","docblock":"The selected event, if any.","doclets":{},"descriptionHtml":"<p>The selected event, if any.</p>\n"},"views":{"type":{"name":"Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')","raw":"componentViews"},"required":false,"description":"An array of built-in view names to allow the calendar to display.\naccepts either an array of builtin view names,\n\n```jsx\nviews={['month', 'day', 'agenda']}\n```\nor an object hash of the view name and the component (or boolean for builtin).\n\n```jsx\nviews={{\n  month: true,\n  week: false,\n  myweek: WorkWeekViewComponent,\n}}\n```\n\nCustom views can be any React component, that implements the following\ninterface:\n\n```js\ninterface View {\n  static title(date: Date, { formats: DateFormat[], culture: string?, ...props }): string\n  static navigate(date: Date, action: 'PREV' | 'NEXT' | 'DATE'): Date\n}\n```","defaultValue":{"value":"[views.MONTH, views.WEEK, views.DAY, views.AGENDA]","computed":false},"name":"views","docblock":"An array of built-in view names to allow the calendar to display.\naccepts either an array of builtin view names,\n\n```jsx\nviews={['month', 'day', 'agenda']}\n```\nor an object hash of the view name and the component (or boolean for builtin).\n\n```jsx\nviews={{\n  month: true,\n  week: false,\n  myweek: WorkWeekViewComponent,\n}}\n```\n\nCustom views can be any React component, that implements the following\ninterface:\n\n```js\ninterface View {\n  static title(date: Date, { formats: DateFormat[], culture: string?, ...props }): string\n  static navigate(date: Date, action: 'PREV' | 'NEXT' | 'DATE'): Date\n}\n```\n\n@type Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')\n@View\n     ['month', 'week', 'day', 'agenda']","doclets":{"type":"Calendar.Views ('month'|'week'|'work_week'|'day'|'agenda')","View":true},"descriptionHtml":"<p>An array of built-in view names to allow the calendar to display.\naccepts either an array of builtin view names,</p>\n<pre><code class=\"lang-jsx\">views<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">[</span><span class=\"token string\">'month'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'day'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'agenda'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">}</span>\n</code></pre>\n<p>or an object hash of the view name and the component (or boolean for builtin).</p>\n<pre><code class=\"lang-jsx\">views<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>\n  month<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">,</span>\n  week<span class=\"token punctuation\">:</span> <span class=\"token boolean\">false</span><span class=\"token punctuation\">,</span>\n  myweek<span class=\"token punctuation\">:</span> WorkWeekViewComponent<span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span>\n</code></pre>\n<p>Custom views can be any React component, that implements the following\ninterface:</p>\n<pre><code class=\"lang-js\"><span class=\"token keyword\">interface</span> <span class=\"token class-name\">View</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">static</span> <span class=\"token function\">title</span><span class=\"token punctuation\">(</span>date<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> formats<span class=\"token punctuation\">:</span> DateFormat<span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span> culture<span class=\"token punctuation\">:</span> string<span class=\"token operator\">?</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span>props <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span> string\n  <span class=\"token keyword\">static</span> <span class=\"token function\">navigate</span><span class=\"token punctuation\">(</span>date<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span> action<span class=\"token punctuation\">:</span> <span class=\"token string\">'PREV'</span> <span class=\"token operator\">|</span> <span class=\"token string\">'NEXT'</span> <span class=\"token operator\">|</span> <span class=\"token string\">'DATE'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span> Date\n<span class=\"token punctuation\">}</span>\n</code></pre>\n"},"drilldownView":{"type":{"name":"string"},"required":false,"description":"The string name of the destination view for drill-down actions, such\nas clicking a date header, or the truncated events links. If\n`getDrilldownView` is also specified it will be used instead.\n\nSet to `null` to disable drill-down actions.\n\n```js\n<BigCalendar\n  drilldownView=\"agenda\"\n/>\n```","defaultValue":{"value":"views.DAY","computed":true},"name":"drilldownView","docblock":"The string name of the destination view for drill-down actions, such\nas clicking a date header, or the truncated events links. If\n`getDrilldownView` is also specified it will be used instead.\n\nSet to `null` to disable drill-down actions.\n\n```js\n<BigCalendar\n  drilldownView=\"agenda\"\n/>\n```","doclets":{},"descriptionHtml":"<p>The string name of the destination view for drill-down actions, such\nas clicking a date header, or the truncated events links. If\n<code>getDrilldownView</code> is also specified it will be used instead.</p>\n<p>Set to <code>null</code> to disable drill-down actions.</p>\n<pre><code class=\"lang-js\"><span class=\"token operator\">&lt;</span>BigCalendar\n  drilldownView<span class=\"token operator\">=</span><span class=\"token string\">\"agenda\"</span>\n<span class=\"token operator\">/</span><span class=\"token operator\">></span>\n</code></pre>\n"},"getDrilldownView":{"type":{"name":"func"},"required":false,"description":"Functionally equivalent to `drilldownView`, but accepts a function\nthat can return a view name. It's useful for customizing the drill-down\nactions depending on the target date and triggering view.\n\nReturn `null` to disable drill-down actions.\n\n```js\n<BigCalendar\n  getDrilldownView={(targetDate, currentViewName, configuredViewNames) =>\n    if (currentViewName === 'month' && configuredViewNames.includes('week'))\n      return 'week'\n\n    return null;\n  }}\n/>\n```","name":"getDrilldownView","docblock":"Functionally equivalent to `drilldownView`, but accepts a function\nthat can return a view name. It's useful for customizing the drill-down\nactions depending on the target date and triggering view.\n\nReturn `null` to disable drill-down actions.\n\n```js\n<BigCalendar\n  getDrilldownView={(targetDate, currentViewName, configuredViewNames) =>\n    if (currentViewName === 'month' && configuredViewNames.includes('week'))\n      return 'week'\n\n    return null;\n  }}\n/>\n```","doclets":{},"descriptionHtml":"<p>Functionally equivalent to <code>drilldownView</code>, but accepts a function\nthat can return a view name. It&#39;s useful for customizing the drill-down\nactions depending on the target date and triggering view.</p>\n<p>Return <code>null</code> to disable drill-down actions.</p>\n<pre><code class=\"lang-js\"><span class=\"token operator\">&lt;</span>BigCalendar\n  getDrilldownView<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token parameter\">targetDate<span class=\"token punctuation\">,</span> currentViewName<span class=\"token punctuation\">,</span> configuredViewNames</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>currentViewName <span class=\"token operator\">===</span> <span class=\"token string\">'month'</span> <span class=\"token operator\">&amp;&amp;</span> configuredViewNames<span class=\"token punctuation\">.</span><span class=\"token function\">includes</span><span class=\"token punctuation\">(</span><span class=\"token string\">'week'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">return</span> <span class=\"token string\">'week'</span>\n\n    <span class=\"token keyword\">return</span> <span class=\"token keyword\">null</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span>\n<span class=\"token operator\">/</span><span class=\"token operator\">></span>\n</code></pre>\n"},"length":{"type":{"name":"number"},"required":false,"description":"Determines the end date from date prop in the agenda view\ndate prop + length (in number of days) = end date","defaultValue":{"value":"30","computed":false},"name":"length","docblock":"Determines the end date from date prop in the agenda view\ndate prop + length (in number of days) = end date","doclets":{},"descriptionHtml":"<p>Determines the end date from date prop in the agenda view\ndate prop + length (in number of days) = end date</p>\n"},"toolbar":{"type":{"name":"bool"},"required":false,"description":"Determines whether the toolbar is displayed","defaultValue":{"value":"true","computed":false},"name":"toolbar","docblock":"Determines whether the toolbar is displayed","doclets":{},"descriptionHtml":"<p>Determines whether the toolbar is displayed</p>\n"},"popup":{"type":{"name":"bool"},"required":false,"description":"Show truncated events in an overlay when you click the \"+_x_ more\" link.","defaultValue":{"value":"false","computed":false},"name":"popup","docblock":"Show truncated events in an overlay when you click the \"+_x_ more\" link.","doclets":{},"descriptionHtml":"<p>Show truncated events in an overlay when you click the &quot;+_x_ more&quot; link.</p>\n"},"popupOffset":{"type":{"name":"union","value":[{"name":"number"},{"name":"shape","value":{"x":{"name":"number","required":false},"y":{"name":"number","required":false}}}]},"required":false,"description":"Distance in pixels, from the edges of the viewport, the \"show more\" overlay should be positioned.\n\n```jsx\n<BigCalendar popupOffset={30}/>\n<BigCalendar popupOffset={{x: 30, y: 20}}/>\n```","name":"popupOffset","docblock":"Distance in pixels, from the edges of the viewport, the \"show more\" overlay should be positioned.\n\n```jsx\n<BigCalendar popupOffset={30}/>\n<BigCalendar popupOffset={{x: 30, y: 20}}/>\n```","doclets":{},"descriptionHtml":"<p>Distance in pixels, from the edges of the viewport, the &quot;show more&quot; overlay should be positioned.</p>\n<pre><code class=\"lang-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BigCalendar</span> <span class=\"token attr-name\">popupOffset</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">30</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BigCalendar</span> <span class=\"token attr-name\">popupOffset</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>x<span class=\"token punctuation\">:</span> <span class=\"token number\">30</span><span class=\"token punctuation\">,</span> y<span class=\"token punctuation\">:</span> <span class=\"token number\">20</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/></span></span>\n</code></pre>\n"},"selectable":{"type":{"name":"enum","value":[{"value":"true","computed":false},{"value":"false","computed":false},{"value":"'ignoreEvents'","computed":false}]},"required":false,"description":"Allows mouse selection of ranges of dates/times.\n\nThe 'ignoreEvents' option prevents selection code from running when a\ndrag begins over an event. Useful when you want custom event click or drag\nlogic","name":"selectable","docblock":"Allows mouse selection of ranges of dates/times.\n\nThe 'ignoreEvents' option prevents selection code from running when a\ndrag begins over an event. Useful when you want custom event click or drag\nlogic","doclets":{},"descriptionHtml":"<p>Allows mouse selection of ranges of dates/times.</p>\n<p>The &#39;ignoreEvents&#39; option prevents selection code from running when a\ndrag begins over an event. Useful when you want custom event click or drag\nlogic</p>\n"},"longPressThreshold":{"type":{"name":"number"},"required":false,"description":"Specifies the number of miliseconds the user must press and hold on the screen for a touch\nto be considered a \"long press.\" Long presses are used for time slot selection on touch\ndevices.","defaultValue":{"value":"250","computed":false},"name":"longPressThreshold","docblock":"Specifies the number of miliseconds the user must press and hold on the screen for a touch\nto be considered a \"long press.\" Long presses are used for time slot selection on touch\ndevices.\n\n@type {number}\n@default 250","doclets":{"type":"{number}","default":"250"},"descriptionHtml":"<p>Specifies the number of miliseconds the user must press and hold on the screen for a touch\nto be considered a &quot;long press.&quot; Long presses are used for time slot selection on touch\ndevices.</p>\n"},"step":{"type":{"name":"number"},"required":false,"description":"Determines the selectable time increments in week and day views","defaultValue":{"value":"30","computed":false},"name":"step","docblock":"Determines the selectable time increments in week and day views","doclets":{},"descriptionHtml":"<p>Determines the selectable time increments in week and day views</p>\n"},"timeslots":{"type":{"name":"number"},"required":false,"description":"The number of slots per \"section\" in the time grid views. Adjust with `step`\nto change the default of 1 hour long groups, with 30 minute slots.","name":"timeslots","docblock":"The number of slots per \"section\" in the time grid views. Adjust with `step`\nto change the default of 1 hour long groups, with 30 minute slots.","doclets":{},"descriptionHtml":"<p>The number of slots per &quot;section&quot; in the time grid views. Adjust with <code>step</code>\nto change the default of 1 hour long groups, with 30 minute slots.</p>\n"},"rtl":{"type":{"name":"bool"},"required":false,"description":"Switch the calendar to a `right-to-left` read direction.","name":"rtl","docblock":"Switch the calendar to a `right-to-left` read direction.","doclets":{},"descriptionHtml":"<p>Switch the calendar to a <code>right-to-left</code> read direction.</p>\n"},"eventPropGetter":{"type":{"name":"func"},"required":false,"description":"Optionally provide a function that returns an object of className or style props\nto be applied to the the event node.\n\n```js\n(\n\tevent: Object,\n\tstart: Date,\n\tend: Date,\n\tisSelected: boolean\n) => { className?: string, style?: Object }\n```","name":"eventPropGetter","docblock":"Optionally provide a function that returns an object of className or style props\nto be applied to the the event node.\n\n```js\n(\n\tevent: Object,\n\tstart: Date,\n\tend: Date,\n\tisSelected: boolean\n) => { className?: string, style?: Object }\n```","doclets":{},"descriptionHtml":"<p>Optionally provide a function that returns an object of className or style props\nto be applied to the the event node.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span>\n    <span class=\"token parameter\">event<span class=\"token punctuation\">:</span> Object<span class=\"token punctuation\">,</span>\n    start<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n    end<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span>\n    isSelected<span class=\"token punctuation\">:</span> boolean</span>\n<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span> className<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> string<span class=\"token punctuation\">,</span> style<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> Object <span class=\"token punctuation\">}</span>\n</code></pre>\n"},"slotPropGetter":{"type":{"name":"func"},"required":false,"description":"Optionally provide a function that returns an object of className or style props\nto be applied to the the time-slot node. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.\n\n```js\n(date: Date, resourceId: (number|string)) => { className?: string, style?: Object }\n```","name":"slotPropGetter","docblock":"Optionally provide a function that returns an object of className or style props\nto be applied to the the time-slot node. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.\n\n```js\n(date: Date, resourceId: (number|string)) => { className?: string, style?: Object }\n```","doclets":{},"descriptionHtml":"<p>Optionally provide a function that returns an object of className or style props\nto be applied to the the time-slot node. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">date<span class=\"token punctuation\">:</span> Date<span class=\"token punctuation\">,</span> resourceId<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span>number<span class=\"token operator\">|</span>string<span class=\"token punctuation\">)</span></span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span> className<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> string<span class=\"token punctuation\">,</span> style<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> Object <span class=\"token punctuation\">}</span>\n</code></pre>\n"},"dayPropGetter":{"type":{"name":"func"},"required":false,"description":"Optionally provide a function that returns an object of className or style props\nto be applied to the the day background. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.\n\n```js\n(date: Date) => { className?: string, style?: Object }\n```","name":"dayPropGetter","docblock":"Optionally provide a function that returns an object of className or style props\nto be applied to the the day background. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.\n\n```js\n(date: Date) => { className?: string, style?: Object }\n```","doclets":{},"descriptionHtml":"<p>Optionally provide a function that returns an object of className or style props\nto be applied to the the day background. Caution! Styles that change layout or\nposition may break the calendar in unexpected ways.</p>\n<pre><code class=\"lang-js\"><span class=\"token punctuation\">(</span><span class=\"token parameter\">date<span class=\"token punctuation\">:</span> Date</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span> className<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> string<span class=\"token punctuation\">,</span> style<span class=\"token operator\">?</span><span class=\"token punctuation\">:</span> Object <span class=\"token punctuation\">}</span>\n</code></pre>\n"},"showMultiDayTimes":{"type":{"name":"bool"},"required":false,"description":"Support to show multi-day events with specific start and end times in the\nmain time grid (rather than in the all day header).\n\n**Note: This may cause calendars with several events to look very busy in\nthe week and day views.**","name":"showMultiDayTimes","docblock":"Support to show multi-day events with specific start and end times in the\nmain time grid (rather than in the all day header).\n\n**Note: This may cause calendars with several events to look very busy in\nthe week and day views.**","doclets":{},"descriptionHtml":"<p>Support to show multi-day events with specific start and end times in the\nmain time grid (rather than in the all day header).</p>\n<p><strong>Note: This may cause calendars with several events to look very busy in\nthe week and day views.</strong></p>\n"},"min":{"type":{"name":"instanceOf","value":"Date"},"required":false,"description":"Constrains the minimum _time_ of the Day and Week views.","name":"min","docblock":"Constrains the minimum _time_ of the Day and Week views.","doclets":{},"descriptionHtml":"<p>Constrains the minimum <em>time</em> of the Day and Week views.</p>\n"},"max":{"type":{"name":"instanceOf","value":"Date"},"required":false,"description":"Constrains the maximum _time_ of the Day and Week views.","name":"max","docblock":"Constrains the maximum _time_ of the Day and Week views.","doclets":{},"descriptionHtml":"<p>Constrains the maximum <em>time</em> of the Day and Week views.</p>\n"},"scrollToTime":{"type":{"name":"instanceOf","value":"Date"},"required":false,"description":"Determines how far down the scroll pane is initially scrolled down.","name":"scrollToTime","docblock":"Determines how far down the scroll pane is initially scrolled down.","doclets":{},"descriptionHtml":"<p>Determines how far down the scroll pane is initially scrolled down.</p>\n"},"culture":{"type":{"name":"string"},"required":false,"description":"Specify a specific culture code for the Calendar.\n\n**Note: it's generally better to handle this globally via your i18n library.**","name":"culture","docblock":"Specify a specific culture code for the Calendar.\n\n**Note: it's generally better to handle this globally via your i18n library.**","doclets":{},"descriptionHtml":"<p>Specify a specific culture code for the Calendar.</p>\n<p><strong>Note: it&#39;s generally better to handle this globally via your i18n library.</strong></p>\n"},"formats":{"type":{"name":"shape","value":{"dateFormat":{"type":{"name":"custom","raw":"dateFormat","description":"Format for the day of the month heading in the Month view.\ne.g. \"01\", \"02\", \"03\", etc","required":false},"description":"Format for the day of the month heading in the Month view.\ne.g. \"01\", \"02\", \"03\", etc","required":false,"name":"dateFormat","docblock":"Format for the day of the month heading in the Month view.\ne.g. \"01\", \"02\", \"03\", etc","doclets":{},"descriptionHtml":"<p>Format for the day of the month heading in the Month view.\ne.g. &quot;01&quot;, &quot;02&quot;, &quot;03&quot;, etc</p>\n"},"dayFormat":{"type":{"name":"custom","raw":"dateFormat","description":"A day of the week format for Week and Day headings,\ne.g. \"Wed 01/04\"","required":false},"description":"A day of the week format for Week and Day headings,\ne.g. \"Wed 01/04\"","required":false,"name":"dayFormat","docblock":"A day of the week format for Week and Day headings,\ne.g. \"Wed 01/04\"","doclets":{},"descriptionHtml":"<p>A day of the week format for Week and Day headings,\ne.g. &quot;Wed 01/04&quot;</p>\n"},"weekdayFormat":{"type":{"name":"custom","raw":"dateFormat","description":"Week day name format for the Month week day headings,\ne.g: \"Sun\", \"Mon\", \"Tue\", etc","required":false},"description":"Week day name format for the Month week day headings,\ne.g: \"Sun\", \"Mon\", \"Tue\", etc","required":false,"name":"weekdayFormat","docblock":"Week day name format for the Month week day headings,\ne.g: \"Sun\", \"Mon\", \"Tue\", etc","doclets":{},"descriptionHtml":"<p>Week day name format for the Month week day headings,\ne.g: &quot;Sun&quot;, &quot;Mon&quot;, &quot;Tue&quot;, etc</p>\n"},"timeGutterFormat":{"type":{"name":"custom","raw":"dateFormat","description":"The timestamp cell formats in Week and Time views, e.g. \"4:00 AM\"","required":false},"description":"The timestamp cell formats in Week and Time views, e.g. \"4:00 AM\"","required":false,"name":"timeGutterFormat","docblock":"The timestamp cell formats in Week and Time views, e.g. \"4:00 AM\"","doclets":{},"descriptionHtml":"<p>The timestamp cell formats in Week and Time views, e.g. &quot;4:00 AM&quot;</p>\n"},"monthHeaderFormat":{"type":{"name":"custom","raw":"dateFormat","description":"Toolbar header format for the Month view, e.g \"2015 April\"","required":false},"description":"Toolbar header format for the Month view, e.g \"2015 April\"","required":false,"name":"monthHeaderFormat","docblock":"Toolbar header format for the Month view, e.g \"2015 April\"","doclets":{},"descriptionHtml":"<p>Toolbar header format for the Month view, e.g &quot;2015 April&quot;</p>\n"},"dayRangeHeaderFormat":{"type":{"name":"custom","raw":"dateRangeFormat","description":"Toolbar header format for the Week views, e.g. \"Mar 29 - Apr 04\"","required":false},"description":"Toolbar header format for the Week views, e.g. \"Mar 29 - Apr 04\"","required":false,"name":"dayRangeHeaderFormat","docblock":"Toolbar header format for the Week views, e.g. \"Mar 29 - Apr 04\"","doclets":{},"descriptionHtml":"<p>Toolbar header format for the Week views, e.g. &quot;Mar 29 - Apr 04&quot;</p>\n"},"dayHeaderFormat":{"type":{"name":"custom","raw":"dateFormat","description":"Toolbar header format for the Day view, e.g. \"Wednesday Apr 01\"","required":false},"description":"Toolbar header format for the Day view, e.g. \"Wednesday Apr 01\"","required":false,"name":"dayHeaderFormat","docblock":"Toolbar header format for the Day view, e.g. \"Wednesday Apr 01\"","doclets":{},"descriptionHtml":"<p>Toolbar header format for the Day view, e.g. &quot;Wednesday Apr 01&quot;</p>\n"},"agendaHeaderFormat":{"type":{"name":"custom","raw":"dateRangeFormat","description":"Toolbar header format for the Agenda view, e.g. \"4/1/2015 — 5/1/2015\"","required":false},"description":"Toolbar header format for the Agenda view, e.g. \"4/1/2015 — 5/1/2015\"","required":false,"name":"agendaHeaderFormat","docblock":"Toolbar header format for the Agenda view, e.g. \"4/1/2015 — 5/1/2015\"","doclets":{},"descriptionHtml":"<p>Toolbar header format for the Agenda view, e.g. &quot;4/1/2015 — 5/1/2015&quot;</p>\n"},"selectRangeFormat":{"type":{"name":"custom","raw":"dateRangeFormat","description":"A time range format for selecting time slots, e.g \"8:00am — 2:00pm\"","required":false},"description":"A time range format for selecting time slots, e.g \"8:00am — 2:00pm\"","required":false,"name":"selectRangeFormat","docblock":"A time range format for selecting time slots, e.g \"8:00am — 2:00pm\"","doclets":{},"descriptionHtml":"<p>A time range format for selecting time slots, e.g &quot;8:00am — 2:00pm&quot;</p>\n"},"agendaDateFormat":{"type":{"name":"custom","raw":"dateFormat","required":false},"description":"","required":false,"name":"agendaDateFormat","docblock":"","doclets":{},"descriptionHtml":""},"agendaTimeFormat":{"type":{"name":"custom","raw":"dateFormat","required":false},"description":"","required":false,"name":"agendaTimeFormat","docblock":"","doclets":{},"descriptionHtml":""},"agendaTimeRangeFormat":{"type":{"name":"custom","raw":"dateRangeFormat","required":false},"description":"","required":false,"name":"agendaTimeRangeFormat","docblock":"","doclets":{},"descriptionHtml":""},"eventTimeRangeFormat":{"type":{"name":"custom","raw":"dateRangeFormat","description":"Time range displayed on events.","required":false},"description":"Time range displayed on events.","required":false,"name":"eventTimeRangeFormat","docblock":"Time range displayed on events.","doclets":{},"descriptionHtml":"<p>Time range displayed on events.</p>\n"},"eventTimeRangeStartFormat":{"type":{"name":"custom","raw":"dateFormat","description":"An optional event time range for events that continue onto another day","required":false},"description":"An optional event time range for events that continue onto another day","required":false,"name":"eventTimeRangeStartFormat","docblock":"An optional event time range for events that continue onto another day","doclets":{},"descriptionHtml":"<p>An optional event time range for events that continue onto another day</p>\n"},"eventTimeRangeEndFormat":{"type":{"name":"custom","raw":"dateFormat","description":"An optional event time range for events that continue from another day","required":false},"description":"An optional event time range for events that continue from another day","required":false,"name":"eventTimeRangeEndFormat","docblock":"An optional event time range for events that continue from another day","doclets":{},"descriptionHtml":"<p>An optional event time range for events that continue from another day</p>\n"}}},"required":false,"description":"Localizer specific formats, tell the Calendar how to format and display dates.\n\n`format` types are dependent on the configured localizer; both Moment and Globalize\naccept strings of tokens according to their own specification, such as: `'DD mm yyyy'`.\n\n```jsx\nlet formats = {\n  dateFormat: 'dd',\n\n  dayFormat: (date, , localizer) =>\n    localizer.format(date, 'DDD', culture),\n\n  dayRangeHeaderFormat: ({ start, end }, culture, localizer) =>\n    localizer.format(start, { date: 'short' }, culture) + ' — ' +\n    localizer.format(end, { date: 'short' }, culture)\n}\n\n<Calendar formats={formats} />\n```\n\nAll localizers accept a function of\nthe form `(date: Date, culture: ?string, localizer: Localizer) -> string`","name":"formats","docblock":"Localizer specific formats, tell the Calendar how to format and display dates.\n\n`format` types are dependent on the configured localizer; both Moment and Globalize\naccept strings of tokens according to their own specification, such as: `'DD mm yyyy'`.\n\n```jsx\nlet formats = {\n  dateFormat: 'dd',\n\n  dayFormat: (date, , localizer) =>\n    localizer.format(date, 'DDD', culture),\n\n  dayRangeHeaderFormat: ({ start, end }, culture, localizer) =>\n    localizer.format(start, { date: 'short' }, culture) + ' — ' +\n    localizer.format(end, { date: 'short' }, culture)\n}\n\n<Calendar formats={formats} />\n```\n\nAll localizers accept a function of\nthe form `(date: Date, culture: ?string, localizer: Localizer) -> string`","doclets":{},"descriptionHtml":"<p>Localizer specific formats, tell the Calendar how to format and display dates.</p>\n<p><code>format</code> types are dependent on the configured localizer; both Moment and Globalize\naccept strings of tokens according to their own specification, such as: <code>&#39;DD mm yyyy&#39;</code>.</p>\n<pre><code class=\"lang-jsx\"><span class=\"token keyword\">let</span> formats <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  dateFormat<span class=\"token punctuation\">:</span> <span class=\"token string\">'dd'</span><span class=\"token punctuation\">,</span>\n\n  <span class=\"token function-variable function\">dayFormat</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\">date<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">,</span> localizer</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span>\n    localizer<span class=\"token punctuation\">.</span><span class=\"token function\">format</span><span class=\"token punctuation\">(</span>date<span class=\"token punctuation\">,</span> <span class=\"token string\">'DDD'</span><span class=\"token punctuation\">,</span> culture<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n\n  <span class=\"token function-variable function\">dayRangeHeaderFormat</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span><span class=\"token parameter\"><span class=\"token punctuation\">{</span> start<span class=\"token punctuation\">,</span> end <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> culture<span class=\"token punctuation\">,</span> localizer</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span>\n    localizer<span class=\"token punctuation\">.</span><span class=\"token function\">format</span><span class=\"token punctuation\">(</span>start<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> date<span class=\"token punctuation\">:</span> <span class=\"token string\">'short'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> culture<span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> <span class=\"token string\">' — '</span> <span class=\"token operator\">+</span>\n    localizer<span class=\"token punctuation\">.</span><span class=\"token function\">format</span><span class=\"token punctuation\">(</span>end<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> date<span class=\"token punctuation\">:</span> <span class=\"token string\">'short'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span> culture<span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Calendar</span> <span class=\"token attr-name\">formats</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>formats<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</code></pre>\n<p>All localizers accept a function of\nthe form <code>(date: Date, culture: ?string, localizer: Localizer) -&gt; string</code></p>\n"},"components":{"type":{"name":"shape","value":{"event":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""},"eventWrapper":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"eventWrapper","docblock":"","doclets":{},"descriptionHtml":""},"eventContainerWrapper":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"eventContainerWrapper","docblock":"","doclets":{},"descriptionHtml":""},"dayWrapper":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"dayWrapper","docblock":"","doclets":{},"descriptionHtml":""},"dateCellWrapper":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"dateCellWrapper","docblock":"","doclets":{},"descriptionHtml":""},"timeSlotWrapper":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"timeSlotWrapper","docblock":"","doclets":{},"descriptionHtml":""},"timeGutterHeader":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"timeGutterHeader","docblock":"","doclets":{},"descriptionHtml":""},"resourceHeader":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"resourceHeader","docblock":"","doclets":{},"descriptionHtml":""},"toolbar":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"toolbar","docblock":"","doclets":{},"descriptionHtml":""},"agenda":{"type":{"name":"shape","value":{"date":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"date","docblock":"","doclets":{},"descriptionHtml":""},"time":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"time","docblock":"","doclets":{},"descriptionHtml":""},"event":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""}},"required":false},"description":"","required":false,"name":"agenda","docblock":"","doclets":{},"descriptionHtml":""},"day":{"type":{"name":"shape","value":{"header":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"header","docblock":"","doclets":{},"descriptionHtml":""},"event":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""}},"required":false},"description":"","required":false,"name":"day","docblock":"","doclets":{},"descriptionHtml":""},"week":{"type":{"name":"shape","value":{"header":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"header","docblock":"","doclets":{},"descriptionHtml":""},"event":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""}},"required":false},"description":"","required":false,"name":"week","docblock":"","doclets":{},"descriptionHtml":""},"month":{"type":{"name":"shape","value":{"header":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"header","docblock":"","doclets":{},"descriptionHtml":""},"dateHeader":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"dateHeader","docblock":"","doclets":{},"descriptionHtml":""},"event":{"type":{"name":"custom","raw":"elementType","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""}},"required":false},"description":"","required":false,"name":"month","docblock":"","doclets":{},"descriptionHtml":""}}},"required":false,"description":"Customize how different sections of the calendar render by providing custom Components.\nIn particular the `Event` component can be specified for the entire calendar, or you can\nprovide an individual component for each view type.\n\n```jsx\nlet components = {\n  event: MyEvent, // used by each view (Month, Day, Week)\n  eventWrapper: MyEventWrapper,\n  eventContainerWrapper: MyEventContainerWrapper,\n  dayWrapper: MyDayWrapper,\n  dateCellWrapper: MyDateCellWrapper,\n  timeSlotWrapper: MyTimeSlotWrapper,\n  timeGutterHeader: MyTimeGutterWrapper,\n  toolbar: MyToolbar,\n  agenda: {\n  \t event: MyAgendaEvent // with the agenda view use a different component to render events\n    time: MyAgendaTime,\n    date: MyAgendaDate,\n  },\n  day: {\n    header: MyDayHeader,\n    event: MyDayEvent,\n  },\n  week: {\n    header: MyWeekHeader,\n    event: MyWeekEvent,\n  },\n  month: {\n    header: MyMonthHeader,\n    dateHeader: MyMonthDateHeader,\n    event: MyMonthEvent,\n  }\n}\n<Calendar components={components} />\n```","name":"components","docblock":"Customize how different sections of the calendar render by providing custom Components.\nIn particular the `Event` component can be specified for the entire calendar, or you can\nprovide an individual component for each view type.\n\n```jsx\nlet components = {\n  event: MyEvent, // used by each view (Month, Day, Week)\n  eventWrapper: MyEventWrapper,\n  eventContainerWrapper: MyEventContainerWrapper,\n  dayWrapper: MyDayWrapper,\n  dateCellWrapper: MyDateCellWrapper,\n  timeSlotWrapper: MyTimeSlotWrapper,\n  timeGutterHeader: MyTimeGutterWrapper,\n  toolbar: MyToolbar,\n  agenda: {\n  \t event: MyAgendaEvent // with the agenda view use a different component to render events\n    time: MyAgendaTime,\n    date: MyAgendaDate,\n  },\n  day: {\n    header: MyDayHeader,\n    event: MyDayEvent,\n  },\n  week: {\n    header: MyWeekHeader,\n    event: MyWeekEvent,\n  },\n  month: {\n    header: MyMonthHeader,\n    dateHeader: MyMonthDateHeader,\n    event: MyMonthEvent,\n  }\n}\n<Calendar components={components} />\n```","doclets":{},"descriptionHtml":"<p>Customize how different sections of the calendar render by providing custom Components.\nIn particular the <code>Event</code> component can be specified for the entire calendar, or you can\nprovide an individual component for each view type.</p>\n<pre><code class=\"lang-jsx\"><span class=\"token keyword\">let</span> components <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  event<span class=\"token punctuation\">:</span> MyEvent<span class=\"token punctuation\">,</span> <span class=\"token comment\">// used by each view (Month, Day, Week)</span>\n  eventWrapper<span class=\"token punctuation\">:</span> MyEventWrapper<span class=\"token punctuation\">,</span>\n  eventContainerWrapper<span class=\"token punctuation\">:</span> MyEventContainerWrapper<span class=\"token punctuation\">,</span>\n  dayWrapper<span class=\"token punctuation\">:</span> MyDayWrapper<span class=\"token punctuation\">,</span>\n  dateCellWrapper<span class=\"token punctuation\">:</span> MyDateCellWrapper<span class=\"token punctuation\">,</span>\n  timeSlotWrapper<span class=\"token punctuation\">:</span> MyTimeSlotWrapper<span class=\"token punctuation\">,</span>\n  timeGutterHeader<span class=\"token punctuation\">:</span> MyTimeGutterWrapper<span class=\"token punctuation\">,</span>\n  toolbar<span class=\"token punctuation\">:</span> MyToolbar<span class=\"token punctuation\">,</span>\n  agenda<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n       event<span class=\"token punctuation\">:</span> MyAgendaEvent <span class=\"token comment\">// with the agenda view use a different component to render events</span>\n    time<span class=\"token punctuation\">:</span> MyAgendaTime<span class=\"token punctuation\">,</span>\n    date<span class=\"token punctuation\">:</span> MyAgendaDate<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  day<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    header<span class=\"token punctuation\">:</span> MyDayHeader<span class=\"token punctuation\">,</span>\n    event<span class=\"token punctuation\">:</span> MyDayEvent<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  week<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    header<span class=\"token punctuation\">:</span> MyWeekHeader<span class=\"token punctuation\">,</span>\n    event<span class=\"token punctuation\">:</span> MyWeekEvent<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  month<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    header<span class=\"token punctuation\">:</span> MyMonthHeader<span class=\"token punctuation\">,</span>\n    dateHeader<span class=\"token punctuation\">:</span> MyMonthDateHeader<span class=\"token punctuation\">,</span>\n    event<span class=\"token punctuation\">:</span> MyMonthEvent<span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Calendar</span> <span class=\"token attr-name\">components</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>components<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</code></pre>\n"},"messages":{"type":{"name":"shape","value":{"allDay":{"type":{"name":"node","required":false},"description":"","required":false,"name":"allDay","docblock":"","doclets":{},"descriptionHtml":""},"previous":{"type":{"name":"node","required":false},"description":"","required":false,"name":"previous","docblock":"","doclets":{},"descriptionHtml":""},"next":{"type":{"name":"node","required":false},"description":"","required":false,"name":"next","docblock":"","doclets":{},"descriptionHtml":""},"today":{"type":{"name":"node","required":false},"description":"","required":false,"name":"today","docblock":"","doclets":{},"descriptionHtml":""},"month":{"type":{"name":"node","required":false},"description":"","required":false,"name":"month","docblock":"","doclets":{},"descriptionHtml":""},"week":{"type":{"name":"node","required":false},"description":"","required":false,"name":"week","docblock":"","doclets":{},"descriptionHtml":""},"day":{"type":{"name":"node","required":false},"description":"","required":false,"name":"day","docblock":"","doclets":{},"descriptionHtml":""},"agenda":{"type":{"name":"node","required":false},"description":"","required":false,"name":"agenda","docblock":"","doclets":{},"descriptionHtml":""},"date":{"type":{"name":"node","required":false},"description":"","required":false,"name":"date","docblock":"","doclets":{},"descriptionHtml":""},"time":{"type":{"name":"node","required":false},"description":"","required":false,"name":"time","docblock":"","doclets":{},"descriptionHtml":""},"event":{"type":{"name":"node","required":false},"description":"","required":false,"name":"event","docblock":"","doclets":{},"descriptionHtml":""},"noEventsInRange":{"type":{"name":"node","required":false},"description":"","required":false,"name":"noEventsInRange","docblock":"","doclets":{},"descriptionHtml":""},"showMore":{"type":{"name":"func","required":false},"description":"","required":false,"name":"showMore","docblock":"","doclets":{},"descriptionHtml":""}}},"required":false,"description":"String messages used throughout the component, override to provide localizations","name":"messages","docblock":"String messages used throughout the component, override to provide localizations","doclets":{},"descriptionHtml":"<p>String messages used throughout the component, override to provide localizations</p>\n"}},"docblock":"react-big-calendar is a full featured Calendar component for managing events and dates. It uses\nmodern `flexbox` for layout, making it super responsive and performant. Leaving most of the layout heavy lifting\nto the browser. __note:__ The default styles use `height: 100%` which means your container must set an explicit\nheight (feel free to adjust the styles to suit your specific needs).\n\nBig Calendar is unopiniated about editing and moving events, preferring to let you implement it in a way that makes\nthe most sense to your app. It also tries not to be prescriptive about your event data structures, just tell it\nhow to find the start and end datetimes and you can pass it whatever you want.\n\nOne thing to note is that, `react-big-calendar` treats event start/end dates as an _exclusive_ range.\nwhich means that the event spans up to, but not including, the end date. In the case\nof displaying events on whole days, end dates are rounded _up_ to the next day. So an\nevent ending on `Apr 8th 12:00:00 am` will not appear on the 8th, whereas one ending\non `Apr 8th 12:01:00 am` will. If you want _inclusive_ ranges consider providing a\nfunction `endAccessor` that returns the end date + 1 day for those events that end at midnight.","doclets":{},"descriptionHtml":"<p>react-big-calendar is a full featured Calendar component for managing events and dates. It uses\nmodern <code>flexbox</code> for layout, making it super responsive and performant. Leaving most of the layout heavy lifting\nto the browser. <strong>note:</strong> The default styles use <code>height: 100%</code> which means your container must set an explicit\nheight (feel free to adjust the styles to suit your specific needs).</p>\n<p>Big Calendar is unopiniated about editing and moving events, preferring to let you implement it in a way that makes\nthe most sense to your app. It also tries not to be prescriptive about your event data structures, just tell it\nhow to find the start and end datetimes and you can pass it whatever you want.</p>\n<p>One thing to note is that, <code>react-big-calendar</code> treats event start/end dates as an <em>exclusive</em> range.\nwhich means that the event spans up to, but not including, the end date. In the case\nof displaying events on whole days, end dates are rounded _up_ to the next day. So an\nevent ending on <code>Apr 8th 12:00:00 am</code> will not appear on the 8th, whereas one ending\non <code>Apr 8th 12:01:00 am</code> will. If you want <em>inclusive</em> ranges consider providing a\nfunction <code>endAccessor</code> that returns the end date + 1 day for those events that end at midnight.</p>\n"}}